<template>
  <div class="probe" v-loading="loading">
    <el-form label-width="120px">
      <el-form-item label="Search Mode">
        <el-select class="mode-select" v-model="searchMode" placeholder="Choose Search Mode">
          <el-option label="Search by Gene Symbol" value="1"></el-option>
          <el-option label="Search by Transcript ID" value="2"></el-option>
        </el-select>
        <el-radio v-model="geneType" label="1">Coding genes</el-radio>
        <el-radio v-model="geneType" label="2">LncRNAs</el-radio>
      </el-form-item>
      <el-form-item label="Return Number">
        <el-input-number v-model="maxNumber" :min="1" :max="20" label="描述文字"></el-input-number>
        <span class="unit-label" v-if="searchMode === '1'">Per Gene</span>
        <span class="unit-label" v-else>Per Transcript</span>
      </el-form-item>
      <el-form-item label="Gene Symbol">
        <el-input type="textarea" :autosize="{ minRows: 3, maxRows: 7}" placeholder="Enter Gene Symbols" v-model="symbolStr" @change="checkInput">
        </el-input>
        <el-tag v-if="!errList.length" class="title-tag" type="success">All Gene Symbol Is Valid!</el-tag>
        <el-tag v-else class="title-tag" type="danger">Invalid Gene Symbols:</el-tag>
        <el-tag class="symbol-tag" v-for="(symbol, index) in errList" :key="index" closable type="warning" @close="removeErr(symbol)">{{symbol}}</el-tag>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search">Search</el-button>
      </el-form-item>
    </el-form>
      <!--<el-cascader expand-trigger="hover" :options="options" v-model="select"></el-cascader>-->
          <el-table :data="dataList" empty-text="Empty!">
            <el-table-column type="index"></el-table-column>
            <el-table-column label="Gene Symbol" prop="geneName"></el-table-column>
            <el-table-column label="Transcript ID" prop="tranName"></el-table-column>
            <el-table-column label="Donor" prop="donor"></el-table-column>
            <el-table-column label="Acceptor" prop="acceptor"></el-table-column>
            <el-table-column label="Data Source" prop="tranName"></el-table-column>
          </el-table>
      <!--<el-collapse v-model="activeNames" @change="handleChange">-->
        <!--<el-collapse-item title="一致性 Consistency" name="1">-->
          <!--<el-table :data="dataList" empty-text="Empty!">-->
            <!--<el-table-column type="index"></el-table-column>-->
            <!--<el-table-column label="Gene Symbol" prop="geneName"></el-table-column>-->
            <!--<el-table-column label="Transcript ID" prop="tranName"></el-table-column>-->
            <!--<el-table-column label="Donor" prop="donor"></el-table-column>-->
            <!--<el-table-column label="Acceptor" prop="acceptor"></el-table-column>-->
            <!--<el-table-column label="Data Source" prop="tranName"></el-table-column>-->
          <!--</el-table>-->
        <!--</el-collapse-item>-->
        <!--<el-collapse-item title="一致性 Consistency" name="2">-->
          <!--<el-table :data="dataList" empty-text="Empty!">-->
            <!--<el-table-column type="index"></el-table-column>-->
            <!--<el-table-column label="Gene Symbol" prop="geneName"></el-table-column>-->
            <!--<el-table-column label="Transcript ID" prop="tranName"></el-table-column>-->
            <!--<el-table-column label="Donor" prop="donor"></el-table-column>-->
            <!--<el-table-column label="Acceptor" prop="acceptor"></el-table-column>-->
            <!--<el-table-column label="Data Source" prop="tranName"></el-table-column>-->
          <!--</el-table>-->
        <!--</el-collapse-item>-->
      </el-collapse>
      <el-pagination class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageno" :page-sizes="[10, 20, 30, 40]" :page-size="perpage" layout="total, sizes, prev, pager, next, jumper" :total="dataCount">
      </el-pagination>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      symbolStr: '',
      symbolList: [],
      errList: [],
      searchMode: '1',
      geneType: '1',
      maxNumber: 10,
      dataList: [],
      dataCount: 0,
      perpage: 10,
      pageno: 1,
      activeNames: ['1']
    }
  },
  watch: {
    symbolList () {
      this.symbolStr = this.symbolList.join('\n')
    }
  },
  methods: {
    removeErr (symbol) {
      this.symbolList.removeByValue(symbol)
      this.errList.removeByValue(symbol)
    },
    checkInput () {
      if (!this.symbolStr) {
        return
      }
      this.$http.post('/api/checkGeneSymbol', {
        searchMode: this.searchMode,
        geneType: this.geneType,
        symbolStr: this.symbolStr
      }).then(res => {
        console.log(res)
        var resData = res.data
        if (resData.errcode === 0) {
          this.symbolList = resData.data.symbolList
          this.errList = resData.data.errList
        }
      })
    },
    search () {
      this.loading = true
      this.$http.post('/api/getProbeList', {
        searchMode: this.searchMode,
        geneType: this.geneType,
        symbolStr: this.symbolStr,
        perpage: this.perpage,
        pageno: this.pageno
      }).then(res => {
        var resData = res.data
        this.loading = false
        if (resData.errcode === 0) {
          this.dataList = resData.data.dataList
          this.dataCount = resData.data.dataCount
        }
      }).catch(() => {
        this.loading = false
      })
    },
    handleSizeChange (val) {
      this.perpage = val
    },
    handleCurrentChange (val) {
      this.pageno = val
      this.search()
    }
  }
}
</script>

<style lang="scss" scoped>
.probe {
  padding: 20px 50px;
  .mode-select {
    width: 250px;
    margin-right: 20px;
  }
  .unit-label {
    font-size: 14px;
    color: #606266;
    margin-left: 20px;
  }
  .title-tag {
    font-size: 14px;
    font-weight: 500;
  }
  .symbol-tag {
    margin-left: 10px;
  }
  .pagination {
    text-align: center;
    margin-top: 10px;
  }
}
</style>



